import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';

// TODO: The latest version of Angular Material has the same UI as implemented here. If applicable, just use the Angular Material toggle component instead.
@Component({
	standalone: true,
	selector: 'findqo-toggle',
	template: `
		<div class="d-flex align-items-center">
			@if (leftLabel) {
				<span class="text-body-regular fg-general-5">{{ leftLabel }}</span>
			}

			<div class="toggle-switch bg-prim-2 mx-2" (click)="onToggle()">
				<div class="toggle-circle bg-basic-1 d-flex align-items-center justify-content-center" [class.active]="checked">
					<img src="assets/icons/check-toggle.svg" height="12px" width="12px" alt="Toggle icon" />
				</div>
			</div>

			@if (rightLabel) {
				<span class="text-body-regular fg-general-5">{{ rightLabel }}</span>
			}
		</div>
	`,
	styles: `
		.toggle-switch {
			width: 36px;
			height: 20px;
			border-radius: 12px;
			display: flex;
			align-items: center;
			cursor: pointer;
			padding: 2px;
			position: relative;

			.toggle-circle {
				width: 16px;
				height: 16px;
				border-radius: 50%;
				position: absolute;
				left: 3px;
				transition: left 0.3s;

				&.active {
					left: 17px;
				}
			}
		}
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ToggleComponent {
	@Input() leftLabel?: string;
	@Input() rightLabel?: string;
	@Input() checked = true;
	@Output() toggled = new EventEmitter<boolean>();

	onToggle(): void {
		this.checked = !this.checked;
		this.toggled.emit(this.checked);
	}
}
